.projGrid{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    justify-content:center;
    align-content:center;
    
    @media (max-width:1100px){
        grid-template-columns: repeat(2,1fr);
    }

    @media (max-width:800px){
        grid-template-columns: repeat(1,1fr);
    }
}